import { html, css, LitElement } from 'lit';
import { store, getState } from '../../data/store/increment-store';

class ContactView extends LitElement {
  constructor() {
      super();

      this.state = getState();

      store.subscribe(() => {
          this.state = getState();
          this.requestUpdate()
      });
  }

  static shadowRootOptions = {...LitElement.shadowRootOptions, mode: 'closed', delegatesFocus: true};

  createRenderRoot() {
    return this;
  }

  render() {
    const { count, increment, decrement } = this.state;

    return html`
      <section>
        <h1>Hubungi Saya ${count}</h1>
        <p>Email: example@example.com</p>
      </section>
    `;
  }
}

customElements.define('contact-view', ContactView);
