import { html, css, LitElement } from 'lit';

class AboutView extends LitElement {
  createRenderRoot() {
    return this;
  }

  render() {
    return html`
      <div class="container">
        <h1 class="text-success">Tentang Saya</h1>
        <p>
          Saya seorang pengembang web dengan fokus pada teknologi modern dan ramah pengguna.
        </p>
      </div>
    `;
  }
}

customElements.define('about-view', AboutView);
