import { html, css, LitElement } from 'lit';
import { store, getState } from '../../data/store/increment-store';

class HomeView extends LitElement {
    constructor() {
        super();

        this.state = getState();

        store.subscribe(() => {
            this.state = getState();
            this.requestUpdate()
        });
    }
    
    static shadowRootOptions = {...LitElement.shadowRootOptions, mode: 'closed', delegatesFocus: true};

    createRenderRoot() {
      return this;
    }

  render() {
    const { count, increment, decrement } = this.state;

    return html`
      <div class="container text-center">
        <h1 class="text-primary">Selamat Datang di Portfolio Saya</h1>
        <p class="lead">Ini adalah halaman utama portfolio saya.</p>
        <p>Count: ${count}</p>
        <button class="btn btn-primary" @click=${increment}>Increment</button>
        <button class="btn btn-secondary" @click=${decrement}>Decrement</button>
      </div>
    `;
  }
}

customElements.define('home-view', HomeView);
